import { h, Component } from "preact";

import CustomInputRow from "./CustomInputRow";

class CustomInputSection extends Component {
  render(props, state) {
    return (
      <section>
        {props.rows.map((current, index) => {
          return (
            <CustomInputRow
              key={index}
              index={index}
              quantity={current.quantity}
              packageType={current.packageType}
              weight={current.weight}
              length={current.length}
              width={current.width}
              height={current.height}
              duplicationHandler={props.duplicationHandler}
              removeHandler={props.removeHandler}
              valueMapper={props.valueMapper}
              addRow={props.addRow}
              totalPackage={props.totalPackage}
              totalWeight={props.totalWeight}
              totalVolume={props.totalVolume}
              rowLength={props.rowLength}
            />
          );
        })}
      </section>
    );
  }
}

export default CustomInputSection;
