import { h, render, Component } from "preact";

import NumberInput from "./components/NumberInput";
import Results from "./components/Results";
import QuoteButton from "./components/QuoteButton";
import CustomInputSection from "./components/customInputSection";

function ceiling(number, significance) {
  return Math.ceil(number / significance) * significance;
}
var autocomplete = new google.maps.places.AutocompleteService();

class Calc extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currency: "IDR",
      currencyValue: "",
      formattedCurrencyValue: "",
      expressValueForTVolume: 0,
      totalValue: "Total value $",
      switchNumber: 2,
      switchNumber1: 2,
      switchStatus: true,
      Country: "",
      State: "",
      City: "",
      PostCode: "",
      places: [],
      address: "",
      rows: [
        {
          quantity: undefined,
          packageType: "carton",
          weight: undefined,
          length: undefined,
          width: undefined,
          height: undefined
        }
      ],
      totalQuantity: 0,
      totalWeight: 0,
      totalVolume: 0,
      totalSeaFreightVolume: 0,
      totalAirFreightVolume: 0,
      totalAirExpressVolume: 0,
      onlyWeight: false
    };
  }

  calculateResult() {
    let totalSeaFreightVolume = 0;
    let totalAirFreightVolume = 0;
    let totalAirExpressVolume = 0;
    let totalQuantity = 0;
    let totalVolume = 0;
    let totalWeight = 0;

    this.state.rows.map(current => {
      let quantity = current.quantity ? parseFloat(current.quantity) : 0;
      let weight = current.weight ? parseFloat(current.weight) : 0;
      let length = current.length ? parseFloat(current.length) : 0;
      let width = current.width ? parseFloat(current.width) : 0;
      let height = current.height ? parseFloat(current.height) : 0;

      let volume = quantity * length * width * height;
      let seaFreightvolume = ceiling(volume / 1000000, 0.001);
      let airFreightVolume = ceiling(volume / 6000, 0.5);
      let airExpressVolume = ceiling(volume / 5000, 0.5);
      let rowWeight = weight * quantity;

      totalSeaFreightVolume += seaFreightvolume;
      totalAirFreightVolume += airFreightVolume;
      totalAirExpressVolume += airExpressVolume;
      totalQuantity += quantity;
      totalVolume += volume / 1000000;
      totalWeight += rowWeight;
    });

    totalSeaFreightVolume = ceiling(totalSeaFreightVolume, 0.5);
    totalAirFreightVolume = ceiling(totalAirFreightVolume, 1);
    totalAirExpressVolume = ceiling(totalAirExpressVolume, 1);

    this.setState({
      totalSeaFreightVolume,
      totalAirFreightVolume,
      totalAirExpressVolume,
      totalQuantity,
      totalWeight,
      totalVolume: totalVolume.toFixed(3)
    });
  }

  handleChangeAddress(evt) {
    evt.preventDefault();
    this.setState({ address: evt.target.value });

    autocomplete.getPlacePredictions(
      {
        input: evt.target.value,
        types: ["(regions)"]
      },
      (predictions, status) => {
        // console.log(predictions, status);
        if (status === "OK") {
          this.setState({
            places: predictions
          });
        }
      }
    );
  }

  handleSwitch() {
    const { switchNumber, switchStatus } = this.state;
    var newNumber = switchNumber;

    if (switchStatus) {
      if (newNumber >= 2) {
        this.setState(
          {
            switchNumber: 52,
            switchStatus: false,
            rows: [
              {
                quantity: undefined,
                packageType: "carton",
                weight: undefined,
                length: undefined,
                width: undefined,
                height: undefined
              }
            ]
          },
          () => {
            this.calculateResult();
          }
        );
      }
    } else {
      if (newNumber <= 52) {
        this.setState({
          switchNumber: 2,
          switchStatus: true
        });
      }
    }
  }

  handleKeyPress(evt) {
    let charCode = evt.which ? evt.which : event.keyCode;
    if (charCode != 46 && charCode > 31 && (charCode < 48 || charCode > 57)) {
      evt.preventDefault();
    }
  }

  handleListClick(v, i) {
    this.setState({ address: v.description, places: [] });

    var places = new google.maps.places.PlacesService(
      document.createElement("div")
    );

    var that = this;
    places.getDetails(
      {
        placeId: v.place_id,
        fields: ["address_components"]
      },
      (place, status) => {
        if (status === google.maps.places.PlacesServiceStatus.OK) {
          console.log(place.address_components);
          let country = place.address_components.filter(current =>
            current.types.includes("country")
          )[0];

          let postCode = place.address_components.filter(current =>
            current.types.includes("postal_code")
          )[0];

          let state = place.address_components.filter(current =>
            current.types.includes("administrative_area_level_1")
          )[0];

          let city = place.address_components.filter(
            current =>
              current.types.includes("locality") ||
              current.types.includes("administrative_area_level_2")
          )[0];

          that.setState({
            Country: country ? country.long_name : "",
            State: state ? state.long_name : "",
            City: city ? city.long_name : "",
            PostCode: postCode ? postCode.long_name : ""
          });
        }
      }
    );
  }

  handlePlaceInput(v, input) {
    if (v === "country") {
      this.setState({ Country: input.target.value });
    } else if (v === "city") {
      this.setState({ City: input.target.value });
    } else if (v === "state") {
      this.setState({ State: input.target.value });
    } else if (v === "postCode") {
      this.setState({ PostCode: input.target.value });
    }
    input.preventDefault();
  }

  handleTotalVolumeField(ev) {
    let input = Number(ev.target.value);
    let express = (input / 5000) * 1000000;
    let cargo = (input / 6000) * 1000000;
    this.setState({
      totalAirExpressVolume: Math.ceil(express),
      totalAirFreightVolume: Math.ceil(cargo),
      expressValueForTVolume: Math.ceil(express),
      totalSeaFreightVolume: input
    });
    ev.preventDefault();
  }
  handleOnlyWeight(evt) {
    let input = evt.target.value;
    if (this.state.expressValueForTVolume === 0) {
      if (input === "") {
        this.setState({
          totalWeight: "",
          totalSeaFreightVolume: "",
          totalAirFreightVolume: "",
          totalAirExpressVolume: "",
          totalWeight: ""
        });
      }
    }
    if (this.state.expressValueForTVolume < input) {
      this.setState({
        totalWeight: input,
        totalSeaFreightVolume: this.state.totalSeaFreightVolume,
        totalAirFreightVolume: input,
        totalAirExpressVolume: input
      });
    }
    if (this.state.totalAirExpressVolume > input) {
      if (input === "") {
        let input1 = this.state.totalSeaFreightVolume;
        let express = (input1 / 5000) * 1000000;
        let cargo = (input1 / 6000) * 1000000;
        this.setState({
          totalSeaFreightVolume: this.state.totalSeaFreightVolume,
          totalAirFreightVolume: Math.ceil(cargo),
          totalAirExpressVolume: Math.ceil(express),
          totalWeight: 0
        });
      } else {
        this.setState({
          totalAirFreightVolume: this.state.totalAirFreightVolume,
          totalAirExpressVolume: this.state.totalAirExpressVolume
        });
      }
    }
    evt.preventDefault();
  }
  render(props, state) {
    // console.log(state);
    const addRow = () => {
      let rows = state.rows;
      rows.push({
        quantity: undefined,
        packageType: "carton",
        weight: undefined,
        length: undefined,
        width: undefined,
        height: undefined
      });
      this.setState({
        rows
      });
    };

    const duplicateRow = index => {
      let newRow = Object.assign({}, state.rows[index]);
      let rows = state.rows;
      rows.splice(index + 1, 0, newRow);
      this.setState({
        rows
      });
      this.calculateResult();
    };

    const removeRow = index => {
      let rows = state.rows;

      if (rows.length > 1) {
        rows.splice(index, 1);
      } else {
        rows = [
          {
            quantity: undefined,
            packageType: "carton",
            weight: undefined,
            length: undefined,
            width: undefined,
            height: undefined
          }
        ];
      }
      this.setState({
        rows
      });
      this.calculateResult();
    };

    const mapValue = (index, name, value) => {
      // map InputRow value to App state rows
      let rows = state.rows;
      rows[index][name] = value;
      this.setState({
        rows
      });
      this.calculateResult();
    };

    return (
      <div>
        <h1 class="main-heading">Shipping Calculator</h1>

        {state.onlyWeight ? null : (
          <section>
            <div class="TopInputContainer">
              <div class="TopInputContainerHeading">
                Where do you want to ship ?
              </div>
              <div>
                <div class="autoCompleteCont">
                  <input
                    id="autocomplete"
                    placeholder="Google Auto address"
                    class="autoCompleteAddress"
                    type="text"
                    value={this.state.address}
                    onInput={this.handleChangeAddress.bind(this)}
                  />
                  {this.state.places !== [] && this.state.places.length > 0 ? (
                    <ul class="autoComplete">
                      {this.state.places.map((v, i) => {
                        return (
                          <li
                            key={i}
                            onClick={() => this.handleListClick(v, i)}
                          >
                            {v.description}
                          </li>
                        );
                      })}
                    </ul>
                  ) : null}
                </div>
                <div class="inputCont">
                  <input
                    placeholder="Country"
                    value={state.Country}
                    onInput={v => this.handlePlaceInput("country", v)}
                    class="inputfield"
                  />
                  <input
                    placeholder="City"
                    value={state.City}
                    onInput={v => this.handlePlaceInput("city", v)}
                    class="inputfield1"
                  />
                </div>
                <div class="inputCont">
                  <input
                    placeholder="State"
                    value={state.State}
                    onInput={v => this.handlePlaceInput("state", v)}
                    class="inputfield"
                  />
                  <input
                    placeholder="Postcode"
                    value={state.PostCode}
                    onInput={v => this.handlePlaceInput("postCode", v)}
                    class="inputfield1"
                  />
                </div>
              </div>

              <div class="pakageHeaderCont">
                <img src={require("./assets/icon1.png")} />
                <div class="pakageHeading">Let’s tell detail of packages</div>
              </div>
            </div>

            <CustomInputSection
              rows={state.rows}
              duplicationHandler={duplicateRow}
              removeHandler={removeRow}
              valueMapper={mapValue}
              addRow={addRow}
              rowLength={state.rows.length}
              totalPackage={state.totalQuantity}
              totalWeight={state.totalWeight}
              totalVolume={state.totalVolume}
            />
          </section>
        )}

        <ul class="lastContainerPackageHide">
          <li>
            <div>
              <img src={require("./assets/icon1.png")} class="packageIcon1" />
              <span class="totalPackage">{`${
                state.totalQuantity
              } Package`}</span>
            </div>
          </li>
          <li>
            <div>
              <img src={require("./assets/time.png")} class="packageIcon2" />
              <span class="packageIconChildRightkgTxt">{`${
                state.totalWeight !== "" ? state.totalWeight : "0"
              } Kg`}</span>
            </div>
          </li>
          <li>
            <div>
              <img src={require("./assets/icon3.png")} class="packageIcon1" />
              <span class="packageTextNumber">{`${
                state.totalVolume
              } cbm`}</span>
            </div>
          </li>
        </ul>

        <p>Enter value of package</p>
        <div class="currency-input-container">
          <select
            class="currency-select"
            onInput={evt => {
              let formattedValue =
                evt.target.value === "IDR"
                  ? Number(state.currencyValue).toLocaleString("id-ID")
                  : Number(state.currencyValue).toLocaleString("en-US");
              this.setState({
                currency: evt.target.value,
                formattedCurrencyValue: formattedValue
              });
            }}
          >
            <option value="IDR">IDR</option>
            <option value="USD">USD</option>
          </select>
          <input
            class="currency-input"
            onKeyPress={this.handleKeyPress}
            placeholder={state.currency}
            onChange={evt => {
              let formattedValue =
                state.currency === "IDR"
                  ? Number(evt.target.value).toLocaleString("id-ID")
                  : Number(evt.target.value).toLocaleString("en-US");
              this.setState({
                currencyValue: evt.target.value,
                formattedCurrencyValue: formattedValue
              });
            }}
            value={state.formattedCurrencyValue}
          />
        </div>

        <div class="switchCont">
          <div
            style={{
              backgroundColor: this.state.switchStatus ? "#CCCCCC" : "#4683ea"
            }}
            class="switch"
            onClick={() => this.handleSwitch()}
          >
            <div
              class="switchCircle circleRight"
              style={{ left: state.switchNumber }}
            />
          </div>
        </div>
        <div class="tVkg">Total volume / Kg</div>

        <div
          class={`${
            state.switchStatus ? "hidden" : ""
          }`}
        >
          <div class="lastContainerTitle">
            <ul>
              <li class="tV">Total volume</li>
              <li class="tW">Total weight</li>
            </ul>
          </div>
          <ul class="lastContainer">
            <li class="lastContainerSChild">
              <ul class="lastContainerSchildUl">
                <li class="lastContainerInput">
                  <input
                    placeholder="V"
                    onKeyPress={this.handleKeyPress}
                    onInput={ev => this.handleTotalVolumeField(ev)}
                    disabled={!state.switchStatus ? false : true}
                  />
                </li>
                <li class="cbmContainer">
                  <div>cbm</div>
                </li>
                <li class="lastContainerInput">
                  <input
                    placeholder="W"
                    onInput={evt => this.handleOnlyWeight(evt)}
                  />
                </li>
                <li class="lastContainerkg">kg</li>
              </ul>
            </li>
          </ul>
        </div>
        {/* //////////        result   /////////// */}
        <Results
          totalAirExpressVolume={this.state.totalAirExpressVolume}
          totalWeight={this.state.totalWeight}
          totalAirFreightVolume={this.state.totalAirFreightVolume}
          totalSeaFreightVolume={this.state.totalSeaFreightVolume}
        />
        <QuoteButton />
      </div>
    );
  }
}

let root = document.getElementById("calc");
render(<Calc />, root, root.lastChild);
