import { h, Component } from "preact";

import NumberInput from "./NumberInput";
import SelectInput from "./SelectInput";

class CustomInputRow extends Component {
  constructor(props) {
    super(props);
    this.state = {
      quantity: props.quantity,
      packageType: props.packageType,
      weight: props.weight,
      length: props.length,
      width: props.width,
      height: props.height
    };
  }
  render(props, state) {
    return (
      <div className="row">
        <div class="pakage1Cont">
          <div class="pakage1ChildCont1">
            <div class="pakage1SiblingContTitle">
              <div class="pakage1SiblingContTitleQ">
                {props.index === 0 ? "Quantity" : null}
              </div>
              <div class="pakage1SiblingContTitlePT">
                {props.index === 0 ? "Package type" : null}
              </div>
              <div class="pakage1SiblingContTitleW">
                {props.index === 0 ? "Weight" : null}
              </div>
            </div>
            <div class="pakage1SiblingContainer">
              <div class="pakageQ">
                <NumberInput
                  label="Quantity"
                  index={props.index}
                  className="cell"
                  placeholder="Q"
                  value={props.quantity}
                  onInput={evt => {
                    this.setState({
                      quantity: evt.target.value
                    });
                    props.valueMapper(props.index, "quantity", state.quantity);
                  }}
                />
              </div>
              <div class="pakageType">
                <SelectInput
                  label="Package type"
                  index={props.index}
                  value={props.packageType}
                  onInput={evt => {
                    this.setState({
                      packageType: evt.target.value
                    });
                    props.valueMapper(
                      props.index,
                      "packageType",
                      state.packageType
                    );
                  }}
                >
                  <option value="carton">Carton</option>
                  <option value="envelope">Envelope</option>
                  <option value="bag">Bag</option>
                  <option value="crate">Crate</option>
                  <option value="box">Box</option>
                  <option value="drum">Drum</option>
                  <option value="pieces">Pieces</option>
                  <option value="document">Document</option>
                </SelectInput>
              </div>
              <div class="pakageW">
                <NumberInput
                  label="Weight"
                  index={props.index}
                  // className="cell"
                  placeholder="W"
                  value={props.weight}
                  onInput={evt => {
                    this.setState({
                      weight: evt.target.value
                    });
                    props.valueMapper(props.index, "weight", state.weight);
                  }}
                />
              </div>
              <div class="pakageUnit">kg</div>
            </div>
            {props.rowLength - 1 === props.index ? (
              <ul class="packageIconContainer">
                <li class="packageIconContainerFirstList">
                  <img
                    src={require("../assets/icon1.png")}
                    class="packageIcon1"
                  />
                  <span class="totalPackage">{`${
                    props.totalPackage
                  } Package`}</span>
                </li>
                <li class="packageIconChildRight">
                  <img
                    src={require("../assets/time.png")}
                    class="packageIcon2"
                  />
                  <span class="packageIconChildRightkgTxt">{`${
                    props.totalWeight
                  } Kg`}</span>
                </li>
              </ul>
            ) : (
              <ul class="packageIconContainer">
                <li>
                  <br />
                </li>
              </ul>
            )}
          </div>

          <div class="pakage1ChildCont2">
            <div class="pakage1SiblingContTitle right">
              <div class="pakage1SiblingRightTitle1">
                {props.index === 0 ? "Specify dimensions" : <br />}
              </div>
            </div>
            <div class="pakage1SiblingContainer1 right">
              <div class="rightIconCont">
                <img class="icon2" src={require("../assets/icon2.png")} />
              </div>
              <div class="lengthCont">
                <NumberInput
                  label="Length"
                  index={props.index}
                  class="lengthInput"
                  placeholder="L"
                  value={props.length}
                  onInput={evt => {
                    this.setState({
                      length: evt.target.value
                    });
                    props.valueMapper(props.index, "length", state.length);
                  }}
                />
              </div>
              <div class="WidthCont">
                <span>X</span>
                <NumberInput
                  label="Width"
                  index={props.index}
                  class="WidthInput"
                  placeholder="W"
                  value={props.width}
                  onInput={evt => {
                    this.setState({
                      width: evt.target.value
                    });
                    props.valueMapper(props.index, "width", state.width);
                  }}
                />
              </div>
              <div class="WidthCont">
                <span>X</span>
                <NumberInput
                  label="Height"
                  index={props.index}
                  class="WidthInput"
                  placeholder="H"
                  value={props.height}
                  onInput={evt => {
                    this.setState({
                      height: evt.target.value
                    });
                    props.valueMapper(props.index, "height", state.height);
                  }}
                />
              </div>
              <div class="pakage1Unit">cm</div>
            </div>
            <ul class="packageIconContainer1">
             <ul class="packageIconChildRight">
                <li onClick={() => props.addRow()}>
                  <img src={require("../assets/add.png")} />{" "}
                </li>
                <li onClick={() => props.duplicationHandler(props.index)}>
                  <img src={require("../assets/copy.png")} />{" "}
                </li>
                <li onClick={() => props.removeHandler(props.index)}>
                  <img class="minusIcon" src={require("../assets/minus.png")} />{" "}
                </li>
              </ul>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default CustomInputRow;
