import { h, Component } from "preact";

const Results = (props) => {
  // render(props, state) {
    // console.log(props);
    return (
      <div class='resultContainer' >
          <ul>
            <li>
              <img src={require('../assets/icon4.png')} class='express' />
              <div class='title' >Express
              <div class='chargeTxtExpress' >Chargeable</div>
              </div>
              <div class='resultText' >
                {Math.max(props.totalAirExpressVolume, props.totalWeight) ? Math.max(props.totalAirExpressVolume, props.totalWeight) : '00'}<span>kg</span>
              </div>
            </li>
            <li>
              <img src={require('../assets/plane.png')} class='plane' />
              <div class='title' >Air cargo
                <div class='chargeTxtCargo' >Chargeable</div>
              </div>
              <div class='resultText' >
              {/* {props.totalAirFreightVolume} */}
                {Math.max(props.totalAirFreightVolume, props.totalWeight) ? Math.max(props.totalAirFreightVolume, props.totalWeight) : '00'}<span>kg</span>
              </div>
              <div class='miniTxt' >Minimum 45 Kg</div>
            </li>
            <li>
              <img src={require('../assets/ship.png')} class='ship' />
              <div class='title' >Ocean
              <div class='chargeTxtOcean' >Chargeable</div>
              </div>
              <div class='resultText' >
                {props.totalSeaFreightVolume ? props.totalSeaFreightVolume : "00"}<span>cbm</span>
              </div>
              <div class='miniTxt' >Minimum  1 cbm</div>
            </li>
          </ul>
        </div>  
    );
  }
// }

export default Results;
